import React from 'react';
import { makeStyles, Typography, Theme, Grid } from '@material-ui/core';
import { graphql, useStaticQuery } from 'gatsby';
import { FluidImageSharp } from 'src/models/image';
import GatsbyImage from 'gatsby-image';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  section: {
    height: '100vh',
    marginBottom: spacing(50),
  },
  header: {
    marginBottom: spacing(3),
  },
  title: {
    fontWeight: 'bold',
  },
  image: {
    width: 550,
  },
}));

const IntroSection = () => {
  const style = useStyles();
  const { rocketImage } = useStaticQuery<{ rocketImage: FluidImageSharp }>(graphql`
    query {
      rocketImage: file(absolutePath: { regex: "/growth-2.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Grid container component="section" className={style.section} alignItems="center">
      <Grid item xs={12} sm>
        <Typography variant="h4" component="h2" gutterBottom={true} className={`${style.whiteSpacePre} ${style.title}`}>
              .
        </Typography>
        <Typography>
                   ?
          <br />
                 ,    ?
          <br />
                 ?
          <br />
          <br />
                     !
        </Typography>
      </Grid>
      <Grid item xs={12} sm="auto" className={style.image}>
        <GatsbyImage fluid={rocketImage.childImageSharp.fluid} />
      </Grid>
    </Grid>
  );
};

export default IntroSection;
