import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import { makeStyles, Theme, createStyles, Typography } from '@material-ui/core';
import Emoji from '../Utils/Emoji';
import AnimatedAngleIcon from '../Animated/AnimatedAngle';
import { useScrollDown } from 'src/hooks/useScrollDown';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      height: `100vh`,
    },
    wrapper: {
      position: 'absolute',
      top: '40%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      [theme.breakpoints.down('md')]: {
        top: '30%',
      },
    },
    title: {
      whiteSpace: 'nowrap',
      fontWeight: theme.typography.fontWeightLight,
      '& strong': {
        color: theme.palette.primary.main,
        fontWeight: theme.typography.fontWeightBold,
      },
    },
    description: {
      margin: 0,
      fontWeight: theme.typography.fontWeightMedium,
    },
    bottomArrow: {
      position: 'absolute',
      bottom: '10%',
      left: '50%',
      transform: 'translateX(-50%)',
    },
  })
);

const MentoringJumbotron = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const styles = useStyles();

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 300,
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 600,
  });
  const bottomArrowAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 900,
  });

  const scrollToBottom = useScrollDown(containerRef, { gutter: 80 });

  return (
    <div ref={containerRef} className={styles.root}>
      <div className={styles.wrapper}>
        <animated.div style={titleAnimation}>
          <Typography variant="h3" component="h1" gutterBottom={true} className={styles.title}>
            .
            <br />
            <strong>  </strong>    <Emoji emoji="" name="hi" />
          </Typography>
        </animated.div>
        <animated.div style={descriptionAnimation}>
          <Typography>
            <strong>  </strong>        
                 IT        
            .
            <br />
                       .
          </Typography>
        </animated.div>
      </div>
      <div className={styles.bottomArrow}>
        <animated.div style={bottomArrowAnimation}>
          <AnimatedAngleIcon onClick={scrollToBottom} />
        </animated.div>
      </div>
    </div>
  );
};

export default MentoringJumbotron;
